
import React from 'react';
import "./Work.css";
import WorkThumbnail from "../WorkThumbnail/WorkThumbnail";
import resume from "../../resume.js";
import Fade from 'react-reveal/Fade';
const Work = (props) => {

  // Default Class to apply to Component
  let classList = `Work`;


  return(
    <div className={classList}>
      <Fade Top><h1 className={'WorkHeader'}>My Previous Work Experience</h1></Fade>
      <div className={'WorkContent'}>

      {resume.workExperience.map((value,index)=>{
        return <Fade right><WorkThumbnail
          {...value}
        /></Fade>
      })}
      </div>
    </div>
  );
}

export default Work;
