
import React from 'react';
import "./ProjectThumbnail.css";
import {Link} from "react-router-dom";
// Function based React Component
const ProjectThumbnail = (props) => {

  let style = {
    backgroundImage:`url(${props.imgUrl})`
  }
  // Default Class to apply to Component
  let classList = `ProjectThumbnail`;

  let deployedUrl = props.deployedUrl !== "" ? <h3 ><a target="_blank" href={props.deployedUrl}>Deployed App</a></h3> : <div/>;
  let githubUrl = props.githubUrl !== "" ? <h3><a target="_blank" href={props.githubUrl}>Github Repo</a></h3> : <div/>;
  return(
    <div className={classList}>
      <div className={'ProjectImage'} style={style}></div>
      <div className={'ProjectTitle'}><h3>{props.title}</h3></div>
      <div className={'project-content'}>
        <h3>{props.title}</h3>
        <p>{props.description}</p>
        {deployedUrl}
        {githubUrl}
      </div>
    </div>
  );
}

export default ProjectThumbnail;
